:root{
  --dark: #374151;
  --darkest:#111827;

}

*{
  margin: 0;
  padding:0;
  font-family:'Times New Roman', Times, serif;
}

h1{
  padding-top: 10px;
  margin-left: 100px;
}

h2{
  text-align: center;
  padding: 2%;
}

h3{
  text-align: center;
  margin-left: 20px;
}

h4{
  padding: 20px 0px 0px 20px;
}

p{
  padding-top: 10px;
}

.app{
  background-color: white;
}

.homepage-img{
  width: 100%;
  height: 90vh;
  margin-bottom:0;
}

.app nav{
  list-style: none;
  margin-left: 70%;
}

.app nav ul li{
  display: inline-block;
  padding: 8px 40px;
  right: 100px;
}

.app nav ul li a{
  font-size: 15px;
  font-weight: 500;
  text-decoration: none;
  top: 20px;
  position: absolute;
  color:black ;
}

.about {
  padding: 30px 100px;
  background: linear-gradient(125deg, var(--darkest),var(--dark));
  min-height: 82vh;
  color: white;
}

.main{
  padding: 30px 100px;
  background: linear-gradient(125deg, var(--darkest),var(--dark));
  min-height: 82vh;
  color: white;
}

.card{
  padding: 20px;
  width: 200px;
  display: inline-block;
  transition: 0.6s ease;
  align-items: center;
}

.react{
  margin-left:70px;
}

.card:hover{
  transform: translateY(-10px);
}

.contact{
  padding: 30px 100px;
  background: linear-gradient(125deg, var(--darkest),var(--dark));
  min-height: 82vh;
  color: white;
}

#form{
  padding: 0 30% 5%;
}

form{
  border: 1px solid black;
  border-radius: 8%;
}

.form-input{
  padding: 5% 20%;
  text-align: center;
}

.form-input input{
  width: 250px;
  height: 30px;
  text-align: center;
}

.form-button{
  position: relative;
  width: 50%;
  height: 30px;
  margin-left: 5%;
}